/**
 * 字符串超过某个长度返回...，否则返回整个字符串
 * @param content 输入的字符串
 * @param limitLength 限制的长度
 * @returns 带...的字符串或者字符串本身
 */
export function getLimitString(content: string, limitLength: number): string {
    if (content?.length > limitLength) {
        return content.slice(0, limitLength - 1) + '...';
    } else {
        return content;
    }
}
